*{
  padding: 0;
  margin: 0;
}
.app{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.board{
  width: calc(64px*8);
  height: calc(64px*8);
  display: flex;
  flex-wrap: wrap;
}
.cell{
  height: 64px;
  width: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cell img{
  width: 48px;
  height: 48px;
  position: relative;

}
.black{
  background-color: aquamarine;
}
.white{
  background-color: lightsalmon;
}
.selected{
  background-color: brown;
}
.available{
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: green;
  
}